<template>
<div id="selectMap">
    <p style="margin-top:-12px">
        <!-- <b>Chrome  https  geolocation .</b> . -->
    </p>
    <div id="map" style="width:100%;height:240px;"></div>
    <!-- <div>
        : <span id="lat">{{ mark_x }}</span>
    </div>
    <div>
        : <span id="lng">{{ mark_y }}</span>
    </div> -->
    <div>
        <span id="address"><p></p>{{ address }}</span>
    </div>
</div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
export default {
    data() {
        return {
            mark_x: 0,
            mark_y: 0,
            address: '',
        }
    },
    computed: {
        ...mapGetters(['getUserLoc']),
    },
    methods: {
        ...mapActions(['findUserLoc']),
    },
    created(){
        this.findUserLoc();
    },
    mounted(){
        const vm = this;
        var geocoder = new kakao.maps.services.Geocoder();
        var mapContainer = document.getElementById('map'), //   div 
        mapOption = { 
            center: new kakao.maps.LatLng(33.450701, 126.570667), //  
            level: 10 //    
        }; 

        var map = new kakao.maps.Map(mapContainer, mapOption); //  
            
        // GeoLocation    
        
        console.log(vm.$store.state.userLoc);
        var locPosition = new kakao.maps.LatLng(vm.getUserLoc.lat, vm.getUserLoc.lng); //    geolocation   
            // message = '<div style="padding:5px;"> ?!</div>'; //   
        
        //   
        vm.mark_x = vm.getUserLoc.lat;
        vm.mark_y = vm.getUserLoc.lng;

        var coord = new kakao.maps.LatLng(locPosition.getLat(), locPosition.getLng());
        var callback = function(result, status) {
            if (status === kakao.maps.services.Status.OK) {
                vm.address = result[0].address.address_name;
            }
        };
        geocoder.coord2Address(coord.getLng(), coord.getLat(), callback);
        
        displayMarker(locPosition);

        //     
        function displayMarker(locPosition) {

            //  
            var marker = new kakao.maps.Marker({  
                map: map, 
                position: locPosition
            }); 
            
            marker.setDraggable(true); 
            //    
            map.setCenter(locPosition);     
            
            kakao.maps.event.addListener(marker, 'dragend', function() {
                //          
                // console.log(marker.getPosition().getLat());
                // console.log(marker.getPosition().getLng());
                vm.mark_x = marker.getPosition().getLat(); 
                vm.mark_y = marker.getPosition().getLng();
                
                var coord = new kakao.maps.LatLng(marker.getPosition().getLat(), marker.getPosition().getLng());
                var callback = function(result, status) {
                    if (status === kakao.maps.services.Status.OK) {
                        vm.address = result[0].address.address_name;
                        vm.$emit('selectLoc-event', { X: vm.mark_x, Y: vm.mark_y, Addr:vm.address})
                    }
                };
                geocoder.coord2Address(coord.getLng(), coord.getLat(), callback);
            });
        }    
    },
}
</script>

