<template>
  <div class="addPost">
    <div class="title-selectPhoto">NEW POST</div>

    <div class="upload-wrap">
      <div class="selectPhoto">
        <p>ORIGINAL   </p>
        
        
        <div class="canvas-wrap">
          <canvas id="previewCanvas"></canvas>
        </div>
 
        <p>
          <input
            v-on:change="fileSelect"
            ref="postImage"
            type="file"
            name="photo"
            id="uploadPhoto"
            required="required"
          />
        </p>
      </div>

      <div class="writingText">
        <p>
          <textarea
            class="textField"
            v-model="post_content"
            wrap="hard"
            placeholder="  ..."
          ></textarea>
        </p>
      </div>
    </div>

    <div class="btn-wrap">
      <div class="modal selectCat">
        <button
          id="show-modal-cat"
          @click=" showModalSelectCat = true"
        ># </button>

        <modal
          v-if="showModalSelectCat"
          @close="showModalSelectCat = false"
        >
          <h3 slot="header">   ?</h3>
          <div
            slot="footer"
          >
            <button
              v-for="nc in nearCats"
              @click="tagCat(`${nc.no}`, `${nc.name}`)"
              :key=nc.no
            >{{ nc.name }}</button>
          </div>

          <div slot="footer" >
            <button @click="showModalSelectCat = false"> </button>
          </div>

        </modal>
      </div>

      <br>

      <div class="modal selectLoc">
        <button
          id="show-modal-loc"
          @click="showModalRegLocation = true"
        > </button>

        <modal
          v-if="showModalRegLocation"
          @close="showModalRegLocation = false"
        >
          <h3 slot="header">   !</h3>

          <div slot="footer">
            <!--  component  -->
            <div id="mapCpnt">
              <mapComponent v-on:selectLoc-event="receiveLoc"></mapComponent>
            </div>

          </div>
          <div slot="footer" >
            <button @click="showModalRegLocation = false"> </button>
          </div>
        </modal>
      </div>

    </div>

    <div class="submit-wrap">
      <p>
        <input
          class="btn-upload"
          v-on:click="submit"
          type="submit"
          value="~!"
        />
      </p>
    </div>
  </div>
</template>


<script>
import axios from "axios";
import Modal from "@/components/post/modal/Modal.vue";
import EXIF from "../../../../node_modules/exif-js/exif";
import mapComponent from '@/components/map/selectlLocationMap'


export default {
  components: { 
    Modal: Modal, 
    mapComponent,

  },
  data() {
    return {
      positions: [
        {
          no: '1', 
          pos_x: 33.450705,
          pos_y: 126.570677,
        },
      ],

      showModalSelectCat: false,
      showModalRegLocation: false,

      cat_no: "",
      user_no: "1", // user  
      post_image: "",
      post_time: "",
      post_content: "",
      post_location: "",
      postImage: "",
      gpsX: "",
      gpsY: "",

      nearCats: [],
      photoGps: [],
      userGps: [],
      gps: [],
      
    };
  },
  created() {
    this.server = this.$store.state.server;
  },

  methods: {
    receiveLoc(rLocation) {
      this.gpsX = rLocation.X
      this.gpsY = rLocation.Y
      this.post_location = rLocation.Addr
      console.log(this.gpsX, this.gpsY)
      console.log(this.post_location)
    },
    toDecimal(gpsInfo) {
      return (
        gpsInfo[0].numerator +
        gpsInfo[1].numerator / (60 * gpsInfo[1].denominator) +
        gpsInfo[2].numerator / (3600 * gpsInfo[2].denominator)
      );
    },
    setGps() {
      //   gps   .
      if(this.photoGps) {
        console.log(" gps  ")
        this.gps = this.photoGps
      }
      //   gps  ,   gps .
      else if (!this.photoGps.latitude || !this.photoGps.longitude) {
        console.log(" gps  ...")
        this.gps = this.userGps;
      }
      //    ,  default 
      else if (!this.userGps.latitude || this.userGps.latitude) {
        console.log("  ..")
        this.gps = {
          latitude: 37.558245,
          longitude: 126.998207
        }
      }
    },
    getUserLoc() {
      //     
      if (navigator.geolocation) {
        var self = this;
        navigator.geolocation.getCurrentPosition(

          function(position) {
            self.userGps = {
              latitude: position.coords.latitude,
              longitude: position.coords.longitude
            };
            alert(" : " + position.coords.latitude + ", : " + position.coords.longitude);
          },

          function(err) {
            console.log("error");
            if (err.code == 1) { alert("Error: Access is denied!"); } 
            else if (err.code == 2) { alert("Error: Position is unavailable!"); }
          },
          { timeout: 30000, enableHighAccuracy: true, maximumAge: 75000 }

        );
      } else {
        alert("  Geolocation  .");
      }
    },
    fileSelect() {
      //   canvas  
      var canvas = document.getElementById("previewCanvas");
      var ctx = canvas.getContext("2d");

      var reader = new FileReader();
      reader.onload = function(event) {
        var img = new Image();

        img.onload = function() {
          var MAX_WIDTH = 420;
          var MAX_HEIGHT = 420;
          var width = img.width;
          var height = img.height;

          //  
          if (width > height) {
            if (width > MAX_WIDTH) {
              height *= MAX_WIDTH / width;
              width = MAX_WIDTH;
            }
          } else {
            if (height > MAX_HEIGHT) {
              width *= MAX_HEIGHT / height;
              height = MAX_HEIGHT;
            }
          }
          canvas.width = width;
          canvas.height = height;
          ctx.drawImage(img, 0, 0, width, height);
        };

        img.src = event.target.result;
      };
      reader.readAsDataURL(event.target.files[0]);

      //    gps 

      // clear photoGps list
      this.photoGps = [];

      if (this.$refs.postImage.files[0]) {
        var self = this;
        // console.log(self)

        EXIF.getData(this.$refs.postImage.files[0], function() {
          // console.log('image info', this )
          if (this.exifdata.GPSLatitude && this.exifdata.GPSLongitude) {
            /*
            console.log(
              ' N', 
              this.exifdata.GPSLatitude[0].numerator, this.exifdata.GPSLatitude[1].numerator, this.exifdata.GPSLatitude[2].numerator 
            )
            console.log(
              ' E', 
              this.exifdata.GPSLongitude[0].numerator, this.exifdata.GPSLongitude[1].numerator, this.exifdata.GPSLongitude[2].numerator
            )
            */

            // photoGps (, , )   
            var lat = EXIF.getTag(this, "GPSLatitude");
            var long = EXIF.getTag(this, "GPSLongitude");
            console.log(lat, long);
            var dec_lat = self.toDecimal(lat);
            var dec_long = self.toDecimal(long);
            self.photoGps = {
              latitude: dec_lat,
              longitude: dec_long
            };
            console.log(dec_lat, dec_long);
          }

          //      gps 
          // self.setGps();
          //   

          

          // gps      
          axios
            .get(self.$store.getters.getServer + `/api/cat/searchAll`)
            .then(res => {
              if (res.data.state == "ok") {
                // clear nearCats list
                self.nearCats = [];
                for (var i = 0; i < res.data.data.length; i++) {
                  self.nearCats.push({
                    no: res.data.data[i].cat_no,
                    name: res.data.data[i].cat_name
                  });
                }
              } else {
                console.log("cat   ");
              }
            })
            .catch(err => {
              console.log("  ");
              console.log(err);
            });
        });
      } else {
        console.log(`it's not a image`);
      }
    },
    tagCat(no, name) {
      //    
      this.cat_no = no;
      // console.log("cat_no: " + no + ", cat_name: " + name + "  !!")
    },
    submit() {
      // postImage  
      // file  Vue    $refs  .
      this.postImage = this.$refs.postImage.files[0];

      // , ,      =>  test  alert 
      if (this.postImage == null) {
        console.log("  ")
        return false;
      }
      if(this.post_location == '') {
        console.log(" !")
        return false;
      }
      if(this.cat_no == '') {
        console.log(" !")
        return false;
      }

      // backend     FormData 
      const fd = new FormData();
      fd.append("cat_no", this.cat_no);
      fd.append("user_no", this.user_no);
      fd.append(
        "post_time",
        Vue.prototype.moment(new Date()).format("YYYY-MM-DD HH:mm:ss")
      );
      fd.append("post_content", this.post_content);
      fd.append("post_location", this.post_location);
      fd.append("post_x", this.gpsX);
      fd.append("post_y", this.gpsY);
      fd.append("catImg", this.postImage);

      // FormData   key .
      // for (let key of fd.entries()) {
      //   console.log(`${key}`);
      // }

      // axios 
      axios
        .post(this.$store.getters.getServer + `/api/post/insert`, fd, {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        })
        .then(res => {
          console.log("200");
          if (res.data.state == "ok") {
            console.log("post  ");
            console.log(res);
          } else {
            console.log("post  ");
          }
        })
        .catch(err => {
          console.log("FAILURE");
          console.log(err);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.addPost {
  width: 600px;
  margin: 0 auto;
  margin-top: 10px;
  margin-bottom: 60px;
  // padding-top: 100px;
  padding-top: 10px;
  padding-bottom: 125px;
}
.canvas-wrap {
  position: relative;
  width: 60%;
}
.canvas-wrap:after {
  content: "";
  display: block;
  // padding-bottom: 100%;
}
#uploadCanvas {
  position: absolute;
  width: 100%;
  height: 100%;
}
.textField {
  // resize: none;
  resize: vertical;
  height: 200px;
  width: 420px;
}
</style>